@charset "utf-8";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
li {
    list-style: none;
}
a {
    text-decoration: none;
    color: inherit;
}
body {
    color: #333;
}

.check {
    max-width: 640px;
    width: calc(100% - 30px);
    margin: 0 auto;
}
.check form input [type="text"] {
    width: 100%;
    height: 50px;
    border: 1px solid #999;
}
button img {
    width: 20px;
    height: 100%;
    object-fit: cover;
    border: 0px solid ;
}
.Check .form {
    position: relative;
}
.Check form input {

}
.Check button {
    border: none;
}
.Check .form button {
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translate(0% , -50%);
    border: none;
}
.Check .input_wrap{
    height: 50px;
}
.Check .input_wrap{
    width: 200px;
    position: relative;
}
.Check .input_wrap button {
    background-color: aqua;
    position: absolute;
}
.check_list ul .item {
    background-color: rgb(205, 225, 255);
    width: 200px;
    height: 80px;
    margin: 20px;
    line-height: 80px;
    border-radius: 20px;
}
.check_list ul .item strong {
    text-align: left;
    margin: 0 50px 0 20px;
    font-size: 16px;
    font-weight: 500;
}
.check_list ul .item button {
    border-radius: 50%;
    border: 0px solid;
}